}

.contact .contact-top-menu ul li a {
  color: #fff;
  text-decoration: none;
  font-size: 18px;
  line-height: 85px;
}

.contact h1 {
  position: absolute;
  font-size: 56px;
  color: #fff;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.contact .contact-top-image {
  position: relative;
  width: 100%;
  min-height: 420px;
  background-image: url("../../../WordPressTheme/assets/img/contact/contact.png");
  background-repeat: no-repeat;
  /* 背景の繰り返し設定 */
  background-position: center;
  /* 背景の位置指定 */
  background-size: cover;
}

.contact .contact-top-image::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: #231815;
  opacity: 0.11;
}

